import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { globalVariableService } from '../../common/globalService';

@Component({
  selector: 'app-one-month-product',
  templateUrl: './one-month-product.component.html',
  styleUrls: ['./one-month-product.component.scss']
})
export class OneMonthProductComponent implements OnInit {
  partnerId: any;
  constructor(
    private route: ActivatedRoute,
    private _globalVariableService: globalVariableService
  ) {
    this.partnerId = this.route.snapshot.paramMap.get('pId');
    this._globalVariableService.setPartnerId(this.partnerId);
  }
  ngOnInit(): void {
  }

}
