import { Component, OnInit } from '@angular/core';
import { globalVariableService } from '../../common/globalService';
import { ActivatedRoute } from '@angular/router';
@Component({
  selector: 'app-pricing',
  templateUrl: './pricing.component.html',
  styleUrls: ['./pricing.component.scss'],
})
export class PricingComponent implements OnInit {
  partnerId: any;
  constructor(
    private route: ActivatedRoute,
    private _globalVariableService: globalVariableService
  ) {
    this.partnerId = this.route.snapshot.paramMap.get('pId');
    this._globalVariableService.setPartnerId(this.partnerId);
  }
  ngOnInit(): void {}
}
